
body{font-family:"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif
}
h1,h2{font-weight:normal
}
h1{text-align:center
}
ul{list-style:none;padding:0;margin:0
}
dd{margin-left:0
}
.wrap{width:1000px;margin:0 auto
}
.bg-gray-lighter{background-color:#eff2f7
}
.color-gray{color:#5e6d82
}
.v-text-right{text-align:right
}
.el-table a{color:#20a0ff
}
.v-about{font-weight:bold;color:#aaa;font-size:16px;position:fixed;right:40px;bottom:20px;text-decoration:none;transition:all .3s
}
.v-about:hover{color:#888;text-decoration:underline
}


