
.v-answer{border:1px solid #eee;border-radius:4px;padding:30px;box-sizing:border-box;height:562px;background-color:rgba(255,255,255,0.8);position:absolute;width:1000px;top:85px
}
.v-answer h2{margin-top:0
}
.v-article{display:-ms-flexbox;display:flex;position:relative;height:100%
}
.v-article .v-main-pic{width:160px
}
.v-article .v-main-pic img{display:block;width:100%
}
.v-article .v-main-pic .block{position:relative
}
.v-article .v-main-pic .block .el-rate{margin-top:5px
}
.v-article .v-main-pic .block .v-score{font-size:20px;position:absolute;right:5px;bottom:-3px;color:#ff9900
}
.v-article .v-desc{padding:0 30px;-ms-flex:1;flex:1;font-size:14px
}
.v-article .v-desc li{padding-bottom:8px
}
.v-article .v-desc li a{color:#20a0ff;text-decoration:none
}
.v-article .v-desc li a:hover{text-decoration:underline
}
.v-article .v-option{width:240px
}
.v-article .v-option .block{margin-bottom:30px;margin-left:24px;height:90px;border-bottom:dotted 1px #DEDEDE
}
.v-article .v-option .block .el-rate{margin-top:10px
}
.v-article .v-option .block .el-rate .el-rate__icon{font-size:26px
}
.v-article .v-option .icon-list li{float:left;width:33.3%;text-align:center;font-size:14px
}
.v-article .v-option .icon-list li span{display:inline-block;line-height:normal;vertical-align:middle;cursor:pointer
}
.v-article .v-option .icon-list li span i{font-size:26px;display:block;margin-bottom:10px;color:#8492a6
}
.v-article .v-option .icon-list li span em{font-style:normal;opacity:0;transition:all .3s
}
.v-article .v-option .icon-list li span:hover i{color:#555
}
.v-article .v-option .icon-list li span:hover em{opacity:1
}
.v-article .v-option .icon-list li span.like-animate{color:#f00;transform:perspective(1px) translateZ(0);box-shadow:0 0 1px transparent;animation:clickaction .3s;transform:scale(1)
}
.v-article .v-option .icon-list li span.like-animate i{color:#f00
}
.v-article .v-option .v-next{position:absolute;right:10px;bottom:50px
}
@keyframes clickaction{
50%{transform:scale(1.2)
}
}


